@import '../../../base';

.restrictions-popup {
  max-width: 700px;
  box-shadow: none;

  @media screen and (max-width: 576px) {
    overflow: auto;
    padding: 0 15px;
  }

  .modal-header {
    border-bottom: none;
  }
  
  .header {
    font-size: $font-size-big;
    font-weight: $font-weight-bold;
    color: $color-title;
    text-align: center;
    margin-bottom: 31px;
    line-height: 1;

    @media screen and (max-width: 576px) {
      font-size: $font-size-regular;
    }

    .sub {
      color: $color-hover;
      font-size: $font-size-small;
    }
  }

  .title {
    font-size: $font-size-smallest;
    font-weight: $font-weight-normal;
    color: $color-caption;

    @media screen and (max-width: 576px) {
      font-size: $font-size-smallest;
    }
  }

  .res-desc {
    font-size: $font-size-tiny;
    font-weight: $font-weight-normal;
    color: $color-body;

    @media screen and (max-width: 576px) {
      font-size: $font-size-tiny;
    }
  }

  .line {
    width: 100%;
    height: 1px;
    background-color: #c8e0e4;
    margin: 10px 0;

    @media screen and (max-width: 576px) {
      margin: 10px 0;
    }
  }

  .button-container {
    // margin-top: 60px;
    display: flex;
    justify-content: center;

    @media screen and (max-width: 576px) {
      margin: 30px 0 50px 0;
    }
  }
}