@import '../../../base';

.promo-popup {
  margin: auto;
  padding-bottom: 0;
  @media screen and (max-width: 576px) {
    padding-left: 15px;
    padding-right: 15px;
  }
  @media screen and (min-width: 576px) {
    max-width: 500px;
    width: calc(100vw - 50px);
  }

  .img-container,
  .main-text-container,
  .caption-text-container,
  .sub-text-container,
  .button-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    p {
      margin: 0;
      text-align: center;
    }
  }

  .main-text-container {
    font-size: 28px;
    font-weight: $font-weight-bold;
    color: $color-hover;

    @media screen and (max-width: 576px) {
      font-size: $font-size-regular * 1.2;
    }
  }

  .caption-text-container {
    font-size: 18px;
    font-weight: $font-weight-bold;
    color: $color-body;

    @media screen and (max-width: 576px) {
      font-size: $font-size-regular;
    }
  }

  .sub-text-container {
    margin-top: 14px;
    margin-bottom: 36px;
    font-size: $font-size-small;
    font-weight:$font-weight-normal;
    color: $color-caption;

    @media screen and (max-width: 576px) {
      font-size: $font-size-smallest;
    }
  }

  .button-container {
    justify-content: flex-start;
    height: 130px;

    button {
      @media screen and (max-width: 576px) {
        max-width: 370px;
        min-width: auto !important;
      }
    }
  }

  .tab-container {
    padding-top: 50px;
  }

  .img-container {
    max-width: 650px;
    margin: 10px auto;
    img {
      max-width: 100%;
    }
  }
  
}
